@font-face {
    font-family: 'outfit-light';
    src: url(../assets/fonts/Outfit-Regular.ttf);
}

@font-face {
    font-family: 'outfit-bold';
    src: url(../assets/fonts/Outfit-Bold.ttf);
}

$Outfit-Bold:'outfit-bold';
$Outfit-Light:'outfit-light';
* {
    margin: 0;
    padding: 0;
}

body {
    box-sizing: border-box;
}

$Cyan: hsl(180, 97%, 60%);
$DarkViolet: hsl(257, 27%, 26%);
$Red: hsl(0, 100%, 32%);
$white:white;

/*Neutral*/

$Gray: hsl(0, 0%, 91%);
$GrayishViolet: hsl(257, 7%, 63%);
$gainsoboro: hsl(0, 0%, 55%);
$VeryDarkBlue: hsl(255, 11%, 22%);
$VeryDarkViolet: hsl(260, 8%, 14%);

/**/

@mixin btn-general {
    padding: 6px 16px;
    border-radius: 20px;
    border: none;
    font-weight: 600;
}

@mixin p-style {
    font-size: 1.1em;
    color: $gainsoboro;
    font-weight: 500;
}