.bg-gray {
    background-color: $Gray;
    padding-top: 75px;
}

.intro-statistics {
    margin: auto;
    margin-top: 40px;
    text-align: center;
    width: 50%;
    h3 {
        font-family: $Outfit-Bold;
        font-size: 3.4em;
        @media (max-width: 1150px) {
            font-size: 2.4em;
        }
    }
    p {
        @include p-style;
        text-align: center;
        margin-bottom: 45px;
    }
}

.card-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: 1250px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.single-card {
    width: 325px;
    height: 370px;
    background-color: $white;
    padding: 15px;
    margin-bottom: 10px;
    h4 {
        font-family: $Outfit-Bold;
        margin-bottom: 30px;
    }
    p {
        @include p-style;
    }
    span {
        position: relative;
        display: block;
        text-align: center;
        width: 75px;
        padding: 14px 0;
        bottom: 55px;
        border-radius: 50%;
        background-color: $VeryDarkViolet;
    }
}

.single-card:nth-child(3) {
    margin-top: 90px;
}

.single-card:nth-child(2) {
    margin-top: 40px;
}

.boost-your-link {
    text-align: center;
    background-image: url(../assets/images/bg-boost-desktop.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $DarkViolet;
    padding: 35px;
    @media (max-width: 950px) {
        margin-top: 25px;
    }
    h2 {
        font-family: $Outfit-Bold;
        font-size: 3.5em;
        color: white;
        @media (max-width: 950px) {
            font-size: 2.5em;
        }
    }
    button {
        display: block;
        margin: auto;
        margin-top: 30px;
        border: none;
        border-radius: 25px;
        padding: 10px 20px;
        font-size: 1.3em;
        font-weight: 800;
        color: $white;
        background-color: $Cyan;
    }
}