header {
    display: flex;
    justify-content: space-between;
    font-size: 1.2rem;
    font-family: $Outfit-Light;
    font-weight: 600;
    margin: 30px 0 35px 0;
    align-items: baseline;
    nav {
        display: flex;
        a {
            text-decoration: none;
            color: $GrayishViolet;
            transition: color linear 0.4s;
            &:hover {
                color: $VeryDarkBlue;
            }
        }
        ul {
            list-style: none;
            li {
                margin-right: 25px;
            }
        }
    }
    button {
        @include btn-general;
        color: $GrayishViolet;
    }
    .btn-sign {
        background-color: $Cyan;
        transition: color linear 0.4s;
        margin-left: 35px;
        &:hover {
            color: $VeryDarkBlue;
        }
    }
    @media(max-width:950px) {
        display: none;
    }
}


/**
*
*Mobile nav*/

.mobile-nav {
    margin-top: 3%;
    div:nth-child(1) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        button {
            border: none;
            font-size: 40px;
            background-color: $white;
        }
    }
    ul {
        margin: auto;
        text-align: center;
        width: 90%;
        list-style: none;
        border-radius: 10px;
        font-size: 1.2em;
        font-family: $Outfit-Bold;
        line-height: 45px;
        padding: 20px;
        color: white;
        background-color: $DarkViolet;
        a {
            all: unset;
        }
        button {
            @include btn-general;
            width: 100%;
            margin: auto;
            color: $white;
            background-color: $Cyan;
        }
    }
    @media(min-width:950px) {
        display: none;
    }
}