footer {
    background-color: $VeryDarkViolet;
    color: $white;
    img {
        filter: invert(100%);
        @media (max-width: 800px) {
            margin-bottom: 30px;
        }
    }
    div {
        @media (max-width: 800px) {
            text-align: center;
        }
    }
    li {
        cursor: pointer;
        &:hover {
            color: $Cyan
        }
    }
    ul {
        list-style: none;
        line-height: 30px;
        p {
            font-family: $Outfit-Light;
            font-weight: 500;
        }
    }
    h5 {
        font-family: $Outfit-Bold;
    }
}

footer article {
    display: flex;
    justify-content: space-between;
    padding: 25px;
    @media (max-width: 800px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    div {
        &:nth-child(1) {
            @media (max-width: 800px) {
                margin-left: 20px;
            }
        }
    }
}

.social-network {
    display: flex;
    li {
        margin: 5px;
    }
}