section {
    display: flex;
    justify-content: space-between;
    margin: 25px 0 50px 0;
    @media (max-width: 950px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    div {
        @media (max-width: 950px) {
            text-align: center;
        }
    }
    .intro {
        @media (max-width: 950px) {
            order: 2;
        }
        p {
            @media (max-width: 950px) {
                font-size: 1rem;
            }
        }
    }
    h2 {
        font-size: 4.3rem;
        font-family: $Outfit-Bold;
        margin-bottom: 18px;
        @media (max-width: 1165px) {
            font-size: 3.5rem;
            margin-top: 20px;
            text-align: center;
        }
        @media (max-width: 950px) {
            font-size: 2.8rem;
        }
        @media (max-width:400px) {
            font-size: 2rem;
        }
    }
    p {
        font-size: 1.2rem;
        color: $GrayishViolet;
        width: 500px;
    }
    button {
        @include btn-general;
        font-size: 20px;
        color: $GrayishViolet;
        padding: 8px 16px;
        margin-top: 20px;
        background-color: $Cyan;
    }
    .section-illustration {
        img {
            height: 425px;
            width: 640px;
            @media (max-width: 1265px) {
                height: 370px;
                width: 350px;
            }
            @media (max-width: 400px) {
                height: 270px;
                width: 200px;
            }
        }
    }
}

.handle-shorting {
    margin-top: 25px;
}

.shorter-link-place {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 80px;
    height: 140px;
    background-image: url(../assets/images/bg-shorten-desktop.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $DarkViolet;
    margin: auto;
    @media (max-width: 950px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;
        border-radius: 10px;
        padding: 100px 0;
    }
    form {
        width: 80%;
        margin: auto;
        font-size: 1.3rem;
        @media (max-width: 950px) {
            font-size: 1.1rem;
        }
        input {
            width: 75%;
            height: 60px;
            font-family: $Outfit-Light;
            padding: 10px;
            outline: none;
            border: none;
            border-radius: 8px;
            @media (max-width: 950px) {
                width: 100%;
            }
        }
        button {
            margin-left: 20px;
            border: none;
            background-color: $Cyan;
            padding: 12px 15px;
            border-radius: 8px;
            font-weight: 500;
            color: $VeryDarkBlue;
            @media (max-width: 950px) {
                width: 100%;
                margin-left: 0;
                margin-top: 25px;
            }
        }
    }
}

.single-card:nth-child(3) {
    margin-top: 80px;
}

.short-Link-Section {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-family: $Outfit-Light;
    border-radius: 4px;
    margin: 15px auto;
    padding: 10px;
    width: 80%;
    background-color: $white;
    @media (max-width: 1150px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .b-line {
        @media (max-width: 1150px) {
            background-color: rgb(223, 223, 223);
            padding: 5px;
        }
    }
    p {
        font-size: 20px;
        text-align: center;
    }
    .result {
        color: $Cyan;
    }
    .link-content {
        display: flex;
        @media (max-width: 1150px) {
            flex-direction: column;
        }
    }
    button {
        display: inline-block;
        font-size: 20px;
        font-weight: 600;
        padding: 4px 22px;
        margin-left: 25px;
        border: none;
        border-radius: 5px;
        color: rgb(255, 255, 255);
        background-color: rgb(41, 179, 179);
        @media (max-width: 1150px) {
            width: 100%;
            align-self: center;
            margin-right: 18px;
        }
    }
    .copied-link {
        background-color: $DarkViolet;
    }
}

.invalid-url {
    border: 3px solid rgb(221, 65, 91) !important;
    &::placeholder {
        color: rgb(221, 65, 91);
    }
}

.invalid-p {
    font-size: 18px;
    color: rgb(230, 86, 86) !important;
    +button {
        background-color: rgb(185, 183, 183) !important;
    }
}

.deleteAll {
    display: block;
    border: none;
    margin: auto;
    padding: 8px 25px;
    border-radius: 4px;
    width: 40%;
    white-space: nowrap;
    font-weight: bold;
    color: $white;
    background-color: rgb(5, 0, 53);
}